    box-sizing: border-box;
    color: white;
    font-size: 1.25em;
  }
  
  *, *:before, *:after {
    box-sizing: inherit;
  }
  
  body {
    padding: 0.5%;
  }
  
  body, #input {
    background: #171e1d;
    font-family: monospace;
  }
  
  /* Input */
  .input-wrapper {
    display: flex;
  }
  
  #input, .input-wrapper {
    color: #53eb9b;
  }
  
  #input {
    flex: 2;
    border: none;
  }
  
  #input:focus {
    outline: none;
  }
  
  /* Output */
  #output-wrapper div {
    display: inline-block;
    width: 100%;
  }
  
  .header-output {
    color: #9d9d9d;
  }
  
  .error-output {
    color: #ff4e4e;
  }`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,sBAAsB;IACtB,YAAY;IACZ,iBAAiB;EACnB;;EAEA;IACE,mBAAmB;EACrB;;EAEA;IACE,aAAa;EACf;;EAEA;IACE,mBAAmB;IACnB,sBAAsB;EACxB;;EAEA,UAAU;EACV;IACE,aAAa;EACf;;EAEA;IACE,cAAc;EAChB;;EAEA;IACE,OAAO;IACP,YAAY;EACd;;EAEA;IACE,aAAa;EACf;;EAEA,WAAW;EACX;IACE,qBAAqB;IACrB,WAAW;EACb;;EAEA;IACE,cAAc;EAChB;;EAEA;IACE,cAAc;EAChB","sourcesContent":["html, body {\n    box-sizing: border-box;\n    color: white;\n    font-size: 1.25em;\n  }\n  \n  *, *:before, *:after {\n    box-sizing: inherit;\n  }\n  \n  body {\n    padding: 0.5%;\n  }\n  \n  body, #input {\n    background: #171e1d;\n    font-family: monospace;\n  }\n  \n  /* Input */\n  .input-wrapper {\n    display: flex;\n  }\n  \n  #input, .input-wrapper {\n    color: #53eb9b;\n  }\n  \n  #input {\n    flex: 2;\n    border: none;\n  }\n  \n  #input:focus {\n    outline: none;\n  }\n  \n  /* Output */\n  #output-wrapper div {\n    display: inline-block;\n    width: 100%;\n  }\n  \n  .header-output {\n    color: #9d9d9d;\n  }\n  \n  .error-output {\n    color: #ff4e4e;\n  }"],"sourceRoot":""}]);
